
/*====================== Breadcumb Css Start ====================*/
.breadcumb {
    position: relative;
    padding: 10px 0px;
   
    z-index: 1;
    @include md-screen {
        padding: 75px 0 30px;
    }
    &__wrapper {
        text-align: center;
    }
    &__title {
        color: hsl(var(--black)); 
        font-weight: 700;
    }
    &__list {
        display: flex;
        flex-wrap: wrap;
        justify-content: left;
    }
    &__item {
        color: hsl(var(--black)); 
        padding: 0 5px;
        font-weight: 400;
        font-size: 16px;
            @include msm-screen {
                font-size: 15px;
            }
        &-text {
            color: hsl(var(--black)); ;
        }
    }
    &__link {
        color: hsl(var(--black));  
        font-weight: 500;
        &:hover {
            color: hsl(var(--base)); ;
        }
    }
}
// ====================== Breadcumb Css End ====================
