}

.card-success-1 {
    background-color: hsl(var(--success)/.3);
    .dashboard-card__icon {
        background-color: hsl(var(--success));
    }
}
.card-primary-1 {
    background-color: hsl(var(--primary)/.4);
    .dashboard-card__icon {
