.about-content{
  display: flex;
  flex-direction: column;
  justify-content: center !important;
  h3{
    color: hsl(var(--base));
  }
}

.why-choose_item{
  .about-sec_thumb{
    position: relative;
    img{
      width: 460px;
    } 
    span{
      background: hsl(var(--base));
      top: 70%;
      left: 0;
      position: absolute;
      border: 1px solid hsl(var(--base));
      height: 100px;
      width: 140px;
      font-size: 32px;
      text-align: center;
      color: hsl(var(--white));
    }
    div{
      p{
        color:  hsl(var(--white));
      }
     
    }
   
  }
}

@media(max-width:425px){
  .why-choose_item{
    display: flex;
    flex-direction: column-reverse;
  }
}