/*======================= Dark variable ======================*/
.dark {
    /*====== Common section dark color Start =====*/
    color: hsl(var(--white));
    background-color: hsl(var(--dark));
    h1, h2, h3, h4, h5, h6,p,a {
        color: hsl(var(--white)) !important;
    }
    .banner-left__content h2 {
        color: hsl(var(--white));
    }
    .header.fixed-header .header-wrapper .menu-wrapper ul.main-menu a {
        color: hsl(var(--white));
    }
    .header-wrapper .menu-right-wrapper ul li i {
        color: hsl(var(--white));
    }
    .banner-left__content span {
        color: hsl(var(--base));
    }
    .header.fixed-header .menu-search-wrap .menu-search-wrap--search-icon i {
        color: hsl(var(--white));
    }
    .banner-left__content p {
        color: hsl(var(--white)/0.6);
    }
    .banner-left__content .btn {
        color: hsl(var(--white));
        &:hover {
            color: hsl(var(--base));
        }
    }
    .contact-list__item-icon {
        color: hsl(var(--white));
    }
    .header {
        background-color:transparent;
    }
    li.login-registration-list__item .social-list__item .social-list__link {
        color: hsl(var(--white)) !important;
        border: 0px !important;
    }
    .menu-search-wrap .menu-search-wrap--search-icon i {
        color: hsl(var(--white));
    }
    p {
        color: hsl(var(--white)/.6);
    }
    .header.fixed-header {
        background-color: hsl(var(--dark));
    }
    .nav-menu .nav-item .nav-link {
        color: hsl(var(--white)) !important;
    }
    .nav-item .nav-link.show[aria-expanded=true] {
        color: hsl(var(--white)) !important;
    }
    .dropdown-menu li .dropdown-item {
        color: hsl(var(--white));
    }
    .nav-menu .nav-item:hover .dropdown-menu {
        background: hsl(var(--dark));
    }
    .dropdown-menu li .dropdown-item {
        color: hsl(var(--white));
    }
    .navbar-toggler.header-button {
        color: hsl(var(--white));
    }
    .header-wrapper .menu-wrapper ul.main-menu a {
        color: hsl(var(--white));
    }
    .btn--base.outline {
        color: hsl(var(--white));
    }
    .contact-list__link {
        color: hsl(var(--white));
    }
    .language-box .select {
        color: hsl(var(--white));
    }

    .toggle-search-box i {
        color: hsl(var(--white));
    }
    .testimonial-item__name {
        color: hsl(var(--white));
    }
    .testimonial-item__designation {
        color: hsl(var(--white));
    }
    .footer-item__title {
        color: hsl(var(--white));
    }
    .footer-menu__link {
        color: hsl(var(--white)/.5);
    }
    .footer-contact-menu__item p {
        color: hsl(var(--white)/.5);
    }
    .social-list__item .social-list__link {
        color: hsl(var(--white));
    }
    .social-list__item .social-list__link:hover {
        color: hsl(var(--base)) !important;
    }

    .custom--accordion .accordion-button {
        color: hsl(var(--white));
    }
    .custom--accordion .accordion-button:not(.collapsed) {
        color: hsl(var(--white));

    }
    .custom--accordion .accordion-body {
        color:hsl(var(--white)/0.6);
    }
    .custom--accordion .accordion-button[aria-expanded=false]::after {
        content: "\f067";
        color: hsl(var(--white));
    }
    .custom--accordion .accordion-button[aria-expanded=true]::after, 
    .custom--accordion .accordion-button[aria-expanded=false]::after {
        color: hsl(var(--white));
    }
    .pagination .page-item .page-link {
        color: hsl(var(--white));
    }
    /** ====  Form   ===*/
    .subscribe-wrap__input input {
        &::placeholder {
            color: hsl(var(--white)) !important;
        }
    }
    .search-box.footer-area input.form--control {
        &::placeholder {
            color: hsl(var(--white)) !important;
        }
    }
    .form--control {
        color: hsl(var(--white)) !important;
        &::placeholder {
            color: hsl(var(--white)) !important;
        }
        &:focus {
            color: hsl(var(--white)/.6); 
        }
        &:disabled, &[readonly] {
            background-color: hsl(var(--dark)/.6);
        }
        &[type="password"] {
            color: hsl(var(--white)/.6);
            &:focus {
                color: hsl(var(--white)/.6);
            }
        }
        &[type="file"] {
            &::file-selector-button {
                color: hsl(var(--white)/.6);
                &:hover {
                    color: hsl(var(--white));
                }
            }
        }
    }
    .form--label {
        color: hsl(var(--white)/0.9);
    }
    .input-group-text {
        color: hsl(var(--dark));
    }
    .choose-domain .input-group.country-code select.select.form--control {
        width: 120px;
        color: hsl(var(--white)) !important;
    }
    .breadcumb__link {
        color: hsl(var(--white)/.6);
    }
    .breadcumb__title {
        color: hsl(var(--white));
    }
    .breadcumb__item {
        color: hsl(var(--white)/.6);
    }
    .breadcumb__item-text {
        color: hsl(var(--white)/.6);
    }
    .blog-sidebar__title {
        color: hsl(var(--white)) !important;
    }
    .contact-info__addres-wrap .single_wrapper .single-info .cont-text h6 a {
        color: hsl(var(--white)/0.6);
    }
    .contactus-form {
        background-color: transparent;
    }
    /* ==== Dashboard ==== */
    .sidebar-menu-list__link {
        color: hsl(var(--white));
    }
    .sidebar-submenu-list__link {
        color: hsl(var(--white));
    }
    .dashboard .pricing-plan-item .pricing-plan-item__bottom button {
        color: hsl(var(--white));
    }
    .form--control:disabled, .dark .form--control[readonly] {
        background-color: hsl(var(--base)/0.6);
    }
    .form--control option {
        background: hsl(var(--dark));
    }
    input:-webkit-autofill, textarea:-webkit-autofill, select:-webkit-autofill {
        -webkit-box-shadow: 0 0 0px 1000px transparent inset;
        -webkit-text-fill-color: hsl(var(--white)/.5) !important;
    }

    /* ===== Table  ====*/
    .table thead tr th {
        background-color: hsl(var(--base)/0.05);
        color: hsl(var(--white));
    }
    .table tbody tr td {
        color: hsl(var(--white));
        border: 1px solid hsl(var(--base)/0.1);
        a {
            color: hsl(var(--white));
            &:hover {
                color: hsl(var(--base));
            }
        }
    }
    .table tbody tr td::before {
        color: hsl(var(--white));
    }

    .modal-content {
        color: var(--bs-modal-color);
        background-color: hsl(var(--dark));
    }
    .btn-close {
        color: hsl(var(--white)) !important;
        filter: invert(1);
    }
    .password-show-hide {
        color: hsl(var(--white)/0.5);
    }
    .about-section .about-bg {
        filter: opacity(0.3);
    }
    /*=========== Common section dark color End ==========*/
    .product-details__content .title {
        color: hsl(var(--white)/0.8);
    }
    .service {
        box-shadow: 0px 20px 30px rgba(1, 6, 20, 0.1);
    }
    .service:hover {
        box-shadow: 0px 15px 30px rgba(61, 90, 125, 0.08);
    }
    .experience {
        box-shadow: 0px 20px 30px rgba(1, 6, 20, 0.1);
        background-color: hsl(var(--dark));
    }
    .experience:hover {
        box-shadow: 0px 15px 30px rgba(61, 90, 125, 0.08);
    }
    .why-choose-item {
        background-color: hsl(var(--dark));
    }
    .testimonial-item {
        background-color: hsl(var(--dark));
    }
    .section-heading__title {
        color: hsl(var(--white));
    }
    .service__content h3 {
        color: hsl(var(--white));
    }
    .service__content a {
        color: hsl(var(--white));
    }
    .pricing-plan-item .pricing-plan-item__top .title {
        color: hsl(var(--white));
    }
    .pricing-plan-item .pricing-plan-item__price .title {
        color: hsl(var(--white));
    }
    .pricing-plan-item .pricing-plan-item__price .title span {
        color: hsl(var(--white)/0.5);
    }
    .product__price-title span a {
        color: hsl(var(--white));
    }
    .product__title a {
        color: hsl(var(--white));
    }
    .product__rating-view a {
        color: hsl(var(--white));
        &:hover {
            color: hsl(var(--white)) !important;
        }
    }
    .ecommerce-product__content .title a {
        color: hsl(var(--white)/0.8);
        &:hover {
            color: hsl(var(--base));
        }
    }
    .client-logos img {
        filter: invert(1);
    }
    .order-wrapper__top h4 {
        color: hsl(var(--white));
    }
    .search-bar.form--control {
        color: hsl(var(--white));
    }
    .search-bar.header-search .form--control::placeholder {
        color: hsl(var(--white));
    }
    .latest-blog__title a {
        color: hsl(var(--white));
    }
    .text-list.style-tag .text-list__link {
        color: hsl(var(--white));
    }
    .contact-map iframe {
        filter: brightness(0.6);
    }
    .modal-header {
        border-bottom: 1px solid hsl(var(--white)/.1);
    }
    .modal-footer {
        background-color: var(--bs-modal-footer-bg);
        border-top: 1px solid hsl(var(--white)/.1);
    }
    .custom--card {
        background-color: transparent;
    }
    .custom--card .card-body {
        background-color: hsl(var(--white)/.1);
    }
    .account-form {
        background-color: transparent;
    }
    .sidebar-menu.show-sidebar {
        background-color: hsl(var(--dark));
    }
    .input-group.country-code .input-group-text {
        color: hsl(var(--white));
    }
    .banner-section .shape-banner-bg {
        filter: brightness(0.2);
    }
    .custom--accordion:last-of-type .accordion-button.collapsed {
        color: hsl(var(--white));
    }
    .footer-item .social-list__item .social-list__link {
        color: hsl(var(--white));
    }
    .bottom-footer .bottom-footer-text {
        color: hsl(var(--white));
    }
    .bottom-footer-menu ul li a {
        color: hsl(var(--white));
    }
    .footer-menu__link::before {
        color: hsl(var(--white));
    }
    .bottom-footer-menu ul li::after {
        background: hsl(var(--white));
    }
    .newsletter-popup-wrap__inner {
        border: 1px solid hsl(var(--base));
        background-color: hsl(var(--dark));
    }
    .newsletter-popup-wrap__close-btn:hover {
        background: hsl(var(--base)/.4);
    }
    .search-box.footer input.form--control {
        color: hsl(var(--white)) !important;
    }
    .custom--tab .nav-item .nav-link {
        color: hsl(var(--white));
        border: 1px solid hsl(var(--white)/0.08) !important;
    }
    /** ====================== Random section dark color ========================*/
    .why-choose-us__topic .item .why-title-cont .content h4 {
        color: hsl(var(--white));
    }
    .experience__icon h3 {
        color: hsl(var(--white));
    }
    .blog-item__title-link {
       color: hsl(var(--white));
       &:hover {
           color: hsl(var(--base));
       }
    }
    .text-list__item {
        color: hsl(var(--white)/.6);
    }
    a.btn--simple {
        color: hsl(var(--white)/.6);
        &:hover {
            color: hsl(var(--base));
        }
    }
    .blog-bottom-wrap .auth-name h5 span {
        color: hsl(var(--white)/0.6);
    }
    .footer-area::after {
        background-color: hsl(var(--black)/.99);
    }
    .bottom-footer {
        background-color: hsl(var(--dark)/0.7);
    }
    .sidebar-menu-wrapper {
        background-color: hsl(var(--dark));
    }
    .social-list.blog-details a.social-list__link {
        color: hsl(var(--white)/0.6) !important;
        background: hsl(var(--dark));
    }
    .social-list.blog-details {
       padding-right: 0px;
    }
    .blog-details {
        box-shadow: none;
    }
    .dashboard_profile .sidebar-menu-list__link {
        color: hsl(var(--white));
    }

    .experience__count h3 {
        color: hsl(var(--white));
    }
    .experience__content h3 {
        color: hsl(var(--white)/0.8);
    }
    .breadcumb::after {
        background: hsl(var(--dark)/0.7);
    }

