    border: 1px solid hsl(var(--base)/0.5);
    a{
        &:hover{
            color: hsl(var(--base)/.5);
        }
    }
}
// side bar
.offcanvas{
    background-color: hsl(var( --dark)) !important;
}

// card btn
.card-actn__btn{
    color: hsl(var(--white));
    border: 1px solid hsl(var(--base)/.5) !important;
  }

// category cards
.category-card_body{
    border: 1px solid hsl(var(--base)/.5) !important;
    box-shadow: 0px 0px 11px 0px hsl(var(--base)/0.3);
    background: hsl(var(--dark)) !important;
    .category-img{
        i{
            color: hsl(var(--white) / .7) !important;
        }    
    }  
}

// shop section
.shop-section{
    form{
        .sidebar_body{
            box-shadow: 0px 0px 6px 0px hsl(var(--base)/0.3);
           
            .sidebar-header{
                .aside-search-box{
                    box-shadow: 0px 0px 11px 0px hsl(var(--base)/0.3);
                    .form--input__field{
                        background-color: hsl(var(--body-color)/0.1);
                        border: 1px solid hsl(var(--base)/.5);
                        color: hsl(var(--white)) !important;
                    }
                    .search-btn {
                        color: hsl(var(--white));
                        border-left: 1px solid hsl(var(--base)/.5) !important ;
                    }
                
                }
            }
            .sidebar-categories{
                max-height: 336px;
                overflow-y: scroll;
                
                .form-check-input{
                   border: 1px solid hsl(var(--base)/.2);
                }
                input[type="checkbox"]:checked{
                    background-color: hsl(var(--base));
                }:focus {
                    border-color: hsl(var(--base));
                    box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 8px hsl(var(--base)/.5);
                  }
                .form-check{
                    padding: 4px 40px;
                }
            }::-webkit-scrollbar {
                width: 6px;
              }
              
              /* Track */
              ::-webkit-scrollbar-track {
                box-shadow: inset 0 0 5px hsl(var(--base)/.2); 
                
              }
               
              /* Handle */
              ::-webkit-scrollbar-thumb {
                background: hsl(var(--base)/.5); 
                
              }
              
              /* Handle on hover */
              ::-webkit-scrollbar-thumb:hover {
                background: hsl(var(--base)); 
              }

            .range-slider-box{
                .price-input{
                    .input-group{
                        .input-group-text{
                            color: hsl(var(--white));
                        }  
                    }
                }

                .sliderr{

                    height: 6px;
                    background-color: hsl(var(--base)/.2);
                    position: relative;
                    .progresss{
                        height: 100%;
                        position: absolute;
                        left: 5%;
                        right: 35%;
                        background-color: hsl(var(--base));
                    }
                }
                .range-input{
                    position: relative;

                    input{
                        position: absolute;
                        top: -5px;
                        height: 4px;
                        width: 100%;
                        background: none;
                        -webkit-appearance: none;
                        pointer-events: none;
                        border-radius: 0px !important;
                    }
                    input[type="range"]::-webkit-slider-thumb{
                        height: 24px;
                        width: 24px;
                        border-radius: 50%;
                        pointer-events: auto;
                        -webkit-appearance: none;
                        background: hsl(var(--base));
                    }
                    input[type="range"]::-webkit-range-thumb{
                        height: 20px;
                        width: 20px;
                        border-radius: 50%;
                        pointer-events: auto;
                        border: none;
                        -webkit-appearance: none;
                        background: hsl(var(--base));
                    }
                }
                
            }
           .sidebar-bottom{
            display: flex;
            justify-content: left;
           }
        }
    }
}

    .echommerce-products .circle2 {
        background-color: hsl(var(--white)/.07);
    }

    .textform--control{
        &::placeholder{
            color: hsl(var( --white)) !important;
        }
        color: hsl(var( --white)) ;
    }

    .contact-section .circle1 {
        background-color: hsl(var(--white)/.07);
       
    }

    .coustome-tabs .active {
        border-color: #692deb #692deb hsl(var(--dark)) !important;
    }
}





  

  
