
/*============= Footer Start Here =======================*/
.footer-area {

    margin-top: auto;
    position: relative;
    z-index: 2;
    // &::after {
    //     position: absolute;
    //     top: 0;
    //     right: 0;
    //     width: 100%;
    //     height: 100%;
    //     z-index: -1;
    //     content: "";
    //     opacity: 0.94;
    //     background-color: hsl(var(--dark));
    // }
    .banner-effect-1 {
  
        position: absolute;
        left: 0;
        width: 725px;
        height: 452px;
        background-color: hsl(var(--base)/0.5);
        border-radius: 0;
        z-index: -1;
        @include sm-screen {
            width: 100%;
        }

    }

    .circle1{
        position: absolute;
        bottom: 0;
        right: 5%;
        height: 200px;
        width: 200px;
        background-color: rgb(226, 222, 181);
        filter: blur(80px);
        -webkit-filter: blur(80px);
        border-radius: 50%;
        z-index: -1;
    }
    .circle2{
        position: absolute;
        height: 200px;
        width: 200px;
        background-color: rgb(111, 155, 238);
        filter: blur(80px);
        -webkit-filter: blur(80px);
        border-radius: 50%;
        z-index: -1;
    }
}

.footer-item {
    .latest-blog__title a {
        color: hsl(var(--white));
    }
    .latest-blog {
        padding-bottom: 15px;
        margin-bottom: 15px;
        border-bottom: 1px solid hsl(var(--white)/0.1);
    }
    span.latest-blog__date {
        color: hsl(var(--white)/.6);
    }
    &__desc {
        color: hsl(var(--body-color));
    }
    &__logo {
        margin-bottom: 20px;
        a {
            img {
                width: 100%;
                height: 100%;
                max-width: 160px;
                max-height: 64px;
                filter: brightness(1);
            }
        } 
    }
    &__title {
        color: hsl(var(--black));
        padding-bottom: 10px;
        margin-bottom: 25px;
        position: relative;
        &::after {
            position: absolute;
            content: "";
            width: 50%;
            height: 3px;
            left: 0;
            bottom: -2px;
            background-color: hsl(var(--base)/.4);
        }
    }
    .social-list {
        margin-top: 30px;
        display: flex;
        gap: 12px;
        @include md-screen {
            margin-top: 20px;
        }
        @include msm-screen {
            margin-top: 15px;
        }
    }
    .social-list__item .social-list__link {
        font-size: 20px;
        border: 0px;
        color: hsl(var(--dark));
        &:hover{
            color: hsl(var(--base)) !important;
        }
    }
}
// Footer List Item
.footer-menu {
    display: flex;
    flex-direction: column;
    margin: -5px 0;
    &__item {
        display: block;
        padding: 5px 0;
    }
    &__link {
        position: relative;
        color: hsl(var(--body-color));
        // &::before {
        //     position: absolute;
        //     content: "\f101";
        //     font-weight: 900;
        //     font-family: "Line Awesome Free";
        //     left: 0;
        //     top: 50%;
        //     transform: translateY(-50%);
        //     color: hsl(var(--body-color));
        //     font-size: 15px;
        // }
        &:hover {
            color: hsl(var(--base));
            margin-left: 4px;
        }
    }
}

// Footer Contact
.footer-contact-menu {
    margin: -6px 0;
    &__item {
        display: flex;
        padding: 1px 0;
        p { 
            color: hsl(var(--white));
        }
        &-icon {
            width: 15px;
            color: hsl(var(--white));
            font-size: 15px;
        }
        &-content {
            width: calc(100% - 15px);
            padding-left: 15px;
        }
    }
}
// ============= Footer End Here =======================


.search-box.footer {
    button {
        position: absolute;
        right: 9px;
        top: 9px;
        border: 1px solid transparent;
    }
    input.form--control {
        padding-right: 135px;
        border-radius: 10px;
        border: 0;
        padding: 16px;
        color: hsl(var(--dark) );
        &::placeholder, :focus {
            color: hsl(var(--body-color));
        }
    }
}

.footer-subscribe-box{
    display: flex;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    align-items: center;
    position: relative;

    .icon {
        position: absolute;
        top: 34%;
        left: 16px;
        font-size: 22px;
        transform: translateY(-50%);
        color: hsl(var(--dark));
    }
    .form--input__field{
        font-weight: 500;
        outline: none;
        width: 100%;
        padding: 14px 44px;
        background-color: hsl(var(--body-color)/0.1);
        border: 1px solid hsl(var(--dark)/0.2);
        color: hsl(var(--dark)) !important;
        line-height: 1;
        padding-right: 120px;
    }
    .search-btn {
        position: absolute;
        top: 0px;
        right: 0px;
        color: hsl(var(--dark));
        border: 1px solid hsl(var(--dark)/ .20) !important;
        border-radius: 0px;
        padding: 14px 44px;
        border: 0px !important;
        border-left: 1px solid hsl(var(--dark)/ .20) !important ;
        @include xxsm-screen{
            padding: 14px 20px;
        }
    }

}


// ============= Bottom Footer End Here =======================
.bottom-footer {
    background-color: hsl(var(--base)/0.05);
    border-top: 1px solid hsl(var(--white) / .01);
    .bottom-footer-text {
        color: hsl(var(--body-color));
        @include md-screen {
            text-align: center;
        }
        @include xsm-screen {
            font-size: 14px;
        }
    }
}

@media(max-width: 770px){
    .bottom-footer{
        display: none;
    } 
}

.bottom-footer-menu {
    ul {
        display: flex;
        justify-content: end;
        align-items: center;
        flex-wrap: wrap;
        @include md-screen {
            justify-content: center;
        }
        li {
            padding-left: 14px;
            margin-left: 14px;
            position: relative;
            &::after {
                content: "";
                position: absolute;
                left: 0px;
                background: hsl(var(--body-color));
                height: 5px;
                width: 5px;
                bottom: 6px;
                border-radius: 50%;
            }
            &:first-child::after {
                content: none;
            }
            a {
                color: hsl(var(--body-color));
                @include xsm-screen {
                    font-size: 14px;
                }
            }
        }
    }
}

// 
.mobile-app-link-items{
    display: flex;
    flex-direction: row;
    gap: 24px;
    .app-link{
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        gap: 12px;
        border: 1px solid hsl(var(--base)/.2);
        padding: 4px 20px;
        .icon-sprite{
            font-size: 24px;
        }
    }
    .app-link-text{
        display: flex;
        flex-direction: column;
        .download{
            font-size: 14px;
            color: rgb(138, 138, 138);
        }
    }
}


// =============Bottom Footer End Here =======================


.subscribe-box{
    margin-top: -80px;
    padding: 40px;
    box-shadow: 20px 20px 50px rgba(202, 202, 202, 0.5);
    backdrop-filter: blur(5px);
    background-color: rgba(255, 255, 255, 0.1);
    border-radius: 24px;
    width: 100%;
    display: inline-flex;
    justify-content: center;
    gap: 20px;
    .subscribe-form{

        form{
            display: inline-flex;
            justify-content: space-around;
            align-items: center;
        }
        input{
            width: 50%;
            border: none;
            background-color: rgba(255, 255, 255, 0.1);
            border-bottom: 2px solid hsl(var(--base));
            outline: none;
        }

        .sub-btn {
            padding: 5px  12px;
            border: 1px solid  hsl(var(--base));
            border-radius: 2px;
            font-size: 16px;
            cursor: pointer;
            color: black;
            background-color: #f0dc00 !important;
            background: linear-gradient(#0a1233 0 0) var(--p, 0) / var(--p, 0) no-repeat ;
            transition: .4s, background-position 0s;
        
            &:hover{
                --p: 100%;
                color: white;
            }
        }
       
    }
}
